<template>
  <v-layout justify-center>
    <v-list three-line>
      <template v-for="(item, index) in items">
        <v-subheader
          v-if="item.header"
          :key="item.header"
          v-text="item.header"
        ></v-subheader>

        <v-divider
          v-else-if="item.divider"
          :key="index"
          :inset="item.inset"
        ></v-divider>

        <v-list-item v-else :key="item.title" @click="enterChatRoom(item.title)" style="min-width:375px;">
          <v-list-item-avatar>
            <v-icon>mdi-account-multiple</v-icon>
            <!-- <v-img :src="item.avatar"></v-img> -->
            <!-- <v-img color="light-blue lighten-1"></v-img> -->
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title v-html="item.title + ' (' + item.userCnt+ ')'">{{}}</v-list-item-title>
            <v-list-item-subtitle v-html="item.subtitle"></v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </v-layout>
</template>

<script>

export default {
  name: "ChatRoomList",
  data() {
    return {
      // items: [
      //   // { header: "Today" },
      //   {
      //     avatar: "https://cdn.vuetifyjs.com/images/lists/1.jpg",
      //     title: "Brunch this weekend?",
      //     subtitle: `<span class="text--primary">Ali Connors</span> &mdash; I'll be in your neighborhood doing errands this weekend. Do you want to hang out?`,
      //   },
      //   { divider: true, inset: true },
      //   {
      //     avatar: "https://cdn.vuetifyjs.com/images/lists/2.jpg",
      //     title: 'Summer BBQ <span class="grey--text text--lighten-1">4</span>',
      //     subtitle: `<span class="text--primary">to Alex, Scott, Jennifer</span> &mdash; Wish I could come, but I'm out of town this weekend.`,
      //   },
      //   { divider: true, inset: true },
      //   {
      //     avatar: "https://cdn.vuetifyjs.com/images/lists/3.jpg",
      //     title: "Oui oui",
      //     subtitle:
      //       '<span class="text--primary">Sandra Adams</span> &mdash; Do you have Paris recommendations? Have you ever been?',
      //   },
      //   { divider: true, inset: true },
      //   {
      //     avatar: "https://cdn.vuetifyjs.com/images/lists/4.jpg",
      //     title: "Birthday gift",
      //     subtitle:
      //       '<span class="text--primary">Trevor Hansen</span> &mdash; Have any ideas about what we should get Heidi for her birthday?',
      //   },
      //   { divider: true, inset: true },
      //   {
      //     avatar: "https://cdn.vuetifyjs.com/images/lists/5.jpg",
      //     title: "Recipe to try",
      //     subtitle:
      //       '<span class="text--primary">Britta Holt</span> &mdash; We should eat this: Grate, Squash, Corn, and tomatillo Tacos.',
      //   },
      // ],
    };
  },
  components: {
  },
  computed: {
    items() {
      return this.$store.getters.getRooms;
    },
    userName() {
      return this.$store.getters.getUserName;
    },
  },
  mounted() {},
  created() {
    if (this.$socket !== undefined) return;
    this.$connect().then(() => {
      this.$socket.on("chat", (data) => {
        this.$store.commit("pushMsgData", data);
      });
      // this.$socket.on("personCnt", (data) => {
      //   this.$store.commit("setUserCnt", data);
      // });
      this.$socket.on("rooms", (data) => {
        
        var items = [];
        data.forEach(element => {
          items.push({
            avatar: "https://cdn.vuetifyjs.com/images/lists/5.jpg",
            title: element.roomName,
            userCnt: element.userCnt,
            subtitle: "",//'<span class="text--primary">Britta Holt</span> &mdash; We should eat this: Grate, Squash, Corn, and tomatillo Tacos.',
          })
          items.push({
            divider: true,
            inset: true,
          })
        });

        this.$store.commit("setRooms", items);
      });
    });
  },
  destroyed() {
    // this.$socket.disconnect();
  },
  methods: {
    enterChatRoom(roomName) {
      this.$router.push({ name: "ChatRoom", params: {roomName: roomName}});
    },
  },
};
</script>

